<template>
    <div class="content">
        <span v-if="!error" class="title"> {{  title }}</span>
        <Show v-if="!error" ref="show"></Show>
        <StateComponent :loading="loading" :error="error"/>
    </div>
</template>

<script>
import Show from '@/components/Show'
import StateComponent from '../components/State.vue'
export default {
    components:{
        Show,
        StateComponent
    },
    data(){
        return{
            title:"",
            md:"",
            loading:false,
            error:false
        }
    },
    methods:{
        getInfo(id){
            this.$store.dispatch("loadArticleInfo",id).then(res => {
                 if(res.length == 0){
                     this.empty = true
                 } 
                 else{
                     this.title = res[0].title
                     this.loadMD(id)
                 }
            })
        },
        loadMD(id){
            this.loading = true
            let file = id + ".md?q="+new Date().getTime()
            this.$axios.get("https://md.jqwong.cn/technology/"+file)
            .then(res => {
                this.md = res.data
                this.$refs.show.$emit("getted")
                this.loading = false
            })
            .catch(err => {
                console.log(err)
                this.loading = false
                this.error = true
            })
        }
    },
    created(){
        //console.log(this.$route.params.id)
        this.getInfo(this.$route.params.id)
    }
}
</script>

<style scoped>
    .title{
        width: 60%;
        margin: 0 auto;
        font-size: 1.5rem;
        display: block;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 10px;
        background: #d1ecf1;
        color: #0c5460;
        border: 1px solid #bee5eb;
    }
</style>


