<template>
    <div>
        <div id="modal" class="fixedAll" v-on:click="closePic()" ref="modal">
			<img src="" id="modalPic"/>
			<div id="modalBg" class="fixedAll"></div>
		</div>
        <div v-html="html" id="write"></div>
    </div>
</template>

<script>
import marked from 'marked'
export default {
    data(){
        return{
            html:''
        }
    },
    methods:{
        compile(md){
            let html = marked(this.$parent.md)
            this.html = html
        },
        setPicClickListener(){
            var modal    = document.getElementById("modal")
            var modalBg  = document.getElementById("modalBg")
            var modalPic = document.getElementById("modalPic")
            var write    = document.getElementById("write")
            var pics     = write.querySelectorAll("img")
            pics.forEach(el => {
                el.onclick = function(){
                    modal.style.display = "flex"
                    modalBg.style.background = "#dadada"
                    modalPic.setAttribute("src",this.getAttribute("src"))
                }
            })
        },
        closePic(){
            this.$refs.modal.style.display = "none"
        }
    },
    updated(){
        let pres = document.querySelectorAll("pre")
        for(let i = 0; i < pres.length; i++){
            hljs.highlightBlock(pres[i]);
            hljs.lineNumbersBlock(pres[i].querySelector("code"))
        }
        let tbs = document.querySelectorAll("table")
        for(let i = 0; i < tbs.length; i++){
            if(tbs[i].className == ""){
                tbs[i].className = "default-tb"
            }
        }
        this.setPicClickListener()
    },
    mounted(){
        this.$on('getted',() => {
            this.compile(this.$parent.md)
        })
        this.compile(this.$parent.md)
    }
}
</script>

<style>
/* @import "../../static/css/md.css"; */
@import "../../static/md/monokai_sublime.min.css";
@import "../../static/md/typora-theme-next/next.css";
</style>


<style scoped>
    #modal{
		display: none;
		align-items: center;
	}
	#modalBg{
		background: transparent;
		opacity: .95;
		transition: all .3s ease-in-out;
	}
	#modalPic{
		width: 100%;
		z-index: 999;
	}
	.fixedAll{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
</style>


