<template>
    <div class="index">
        <div class="info" v-if="height != 0" :style="{ height:height + 'px' }">
            <img src="/static/img/avatar.svg" class="avatar"/>
            <span class="tag">人生哪有多如意，万事但求半称心。</span>
            <div class="link">
                <a href="https://github.com/Jquil"><img src="/static/img/github.svg"/></a>
                &nbsp;&nbsp;&nbsp;
                <a href="mailto:835292266@qq.com"><img src="/static/img/mail.svg"/></a>
            </div>
        </div>
        <Footer ref="footer"/>
    </div>
</template>

<script>
import Footer from '@/components/Footer'
export default {
  name: 'Index',
  components:{
    Footer
  },
  computed:{
      height(){
          return this.$store.state.index.height
      }
  },
  mounted(){
      if(this.height == 0){
        this.getHeight()
      }
  },
  methods:{
      getHeight:function(){
          let height = window.innerHeight - this.$parent.$refs.header.$el.clientHeight - this.$refs.footer.$el.clientHeight
          this.$store.commit('setHeight',height)
          //console.log(height)
      }
  }
}
</script>

<style scoped>
    @media screen and (max-width:1024px) {
        .avatar{
            width: 10rem;
        }
        .info>.tag{
            padding: 20px 0 20px 0;
        }
    }
    @media screen and (max-width:1440px) and (min-width:1024px) {
        .avatar{
            width: 15rem;
        }
        .info>.tag{
            padding: 20px 0 20px 0;
        }
    }
    @media screen and (max-width:1920px) and (min-width: 1440px) {
        .avatar{
            width: 22rem;
        }
        .info>.tag{
            padding: 30px 0 30px 0;
        }
    }
    .info{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .info>.tag{
        display: block;
    }
    .info>.link img{
        width: 2rem;
    }
</style>


