<template>
  <div id="app">
    <div id="box">
      <Header ref="header"/>
      <router-view ref="routerView"/>
      <span class="btn-top" ref="btnTop" v-on:click="toTop"><img src="/static/img/top.svg"/></span>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
export default {
  name: 'App',
  components:{
    Header
  },
  data(){
    return {
      lastLoadTime:0,
      loadTimer:1500,     // 每次加载间隔时间
      showDistance:500    // 滚动距离：显示置顶按钮
    }
  },
  methods:{
    listenScroll:function(){
      let scrollTop    = document.documentElement.scrollTop || document.body.scrollTop
      let clientHeight = document.documentElement.clientHeight
      let scrollHeight = document.documentElement.scrollHeight
      let distance     = Math.abs(scrollHeight - parseInt(scrollTop + clientHeight))

      // 显示/隐藏置顶按钮
      if(scrollTop > this.showDistance){
        this.$refs.btnTop.className = "btn-top ani-show"  
      }
      else{
        let className = this.$refs.btnTop.className
        if(className == "btn-top ani-show"){
          this.$refs.btnTop.className = "btn-top ani-hide"
        }
      }

      // 到达底部
      if(distance >= 0 && distance <= 3){
        let nowTime = new Date().getTime()
        if(nowTime - this.lastLoadTime < this.loadTimer){
          return
        }
        this.lastLoadTime = nowTime
        this.$refs.routerView.$emit('inBottom')
      }
    },
    toTop:function(){
      window.scrollTo(0,0)
    }
  },
  created(){
    window.addEventListener("scroll",this.listenScroll)
  }
}
</script>

<style>

@media screen and (max-width:1024px) {
  .content{
    width: 100%;
  }        
}
@media screen and (max-width:1440px) and (min-width:1024px) {
  .content{
    width: 70%;
  } 
}
@media screen and (max-width:1920px) and (min-width: 1440px) {
  .content{
    width: 60%;
  } 
}

html,body,#app{
  padding: 0;
  margin: 0;
  width: 100%;
  overflow-x: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#box{
  width: 90%;
  margin: 0 auto;
}

.content{
  margin: 0 auto;
}

.tip-load{
  display: inline-block;
  margin-bottom: 25px;
  color: #004085;
  background: #cce5ff;
  padding: 5px 10px 5px 10px;
}

.tip-warn{
    padding: 5px 10px 5px 10px;
    background: #fff3cd;
    color: #856404;
    margin: 0 auto;
}

.btn-top{
  position: fixed;
  width: 35px;
  height: 35px;
  right: -35px;
  bottom: 35px;
  background: salmon;
  border-radius: 35px;
  box-shadow: 0px 2px 1px #dadada;
  cursor: pointer;
  text-align: center;
}

.btn-top>img{
  width: 20px;
  margin-top: 7.5px;
}

.ani-hide{
  animation: hide .2s ease-in-out forwards;
}
.ani-show{
  animation: show .2s ease-in-out forwards;
}
@keyframes hide{
  from{
    right: 35px;
  }
  to{
    right: -35px;
  }
}
@keyframes show{
  from{
    right: -35px;
  }
  to{
    right: 35px;
  }
}
</style>



