<template>
    <div class="content">
        <div v-for="item in showArr" :key="item.fileId" class="item">
            <img  class="bg" :src="item.imgUrl"/>
            <router-link v-if="item.type == 'article'" :to="{ name:'article-detail',params:{ id:item.fileId } }"><span class="title" v-html="item.title"></span></router-link>
            <span class="date">{{ item.date }}</span>
        </div>
        <StateComponent :loading="loading" :loaded="loaded" :empty="empty"/>
    </div>
</template>


<script>
import StateComponent from '../components/State.vue'
export default {
    name:"Search",
    components:{
        StateComponent
    },
    data(){
        return{
            key:"",
            type:"",
            page:0,
            loading:false,
            loaded:false,
            empty:false,
            error:false,
            itemSize:20,
            filterArr:[],
            showArr:[],
            reg:{}
        }
    },
    computed:{
        data_article:{
            get(){
                return this.$store.state.article.allData   
            }
        }
    },
    methods:{
        handle(){
            switch(this.type){
                case "article":
                    this.searchByArticle()
                    break;
            }
        },
        searchByArticle(){
            this.reg = new RegExp(this.key,'i')
            this.data_article.forEach(item => {
                if(this.reg.test(item.title)){
                    this.filterArr.push(item)
                }
            })
            this.load()
        },
        load(){
            if(this.loaded || this.error || this.empty)
                return
            this.loading = true
            let start = this.page * this.itemSize,
            end = start + this.itemSize > this.filterArr.length ? this.filterArr.length : start + this.itemSize
            //console.log(end == filterArr.length)
            this.filterArr.slice(start,end).forEach(item => {
                let title = item.title.replace(this.reg,(str) => {
                    return `<span style="color:orangered;">${str}</span>`
                })
                this.showArr.push({
                    title:title,
                    imgUrl:item.imgUrl,
                    fileId:item.fileId,
                    date:item.date,
                    type:"article"
                })
            })
            this.page++
            this.loading = false
            if(end == this.filterArr.length){
                this.loaded = true
            }

            if(this.page == 1 && this.filterArr.length == 0){
                this.empty = true
            }
        },
        reset(){
            this.filterArr = []
            this.showArr   = []
            this.loading   = false
            this.loaded    = false
            this.empty     = false
            this.error     = false
            this.page      = 0
        }
    },
    created(){
        this.key  = this.$route.query.key
        this.type = this.$route.query.type
        this.handle()
    },
    mounted(){
        if(this.filterArr.length == 0){
            this.reset()
            this.empty = true
        }

        this.$on('inBottom',()=>{
            this.load()
        })
    },
    watch: {
        $route(to, from) {
            if(from.query.key != to.query.key){
                this.type = to.query.type == "search" ? from.query.type : to.query.type
                this.key  = to.query.key
                this.reset()
                this.handle()
            }
        }
    }
}
</script>


<style scoped>
    @media screen and (max-width:1024px) {
        .item{
            width: 80%;
            height: 100px;
            margin: 0 auto;
            margin-bottom: 25px;
        }
        .title{
            line-height: 100px;
            font-size: 32px;
        }     
    }
    @media screen and (max-width:1440px) and (min-width:1024px) {
        .item{
            width: 70%;
            height: 150px;
            margin: 0 auto;
            margin-bottom: 55px;
        }
        .title{
            line-height: 150px;
            font-size: 45px;
        }
    }
    @media screen and (max-width:1920px) and (min-width: 1440px) {
        .item{
            width: 70%;
            height: 150px;
            margin: 0 auto;
            margin-bottom: 55px;
        }
        .title{
            line-height: 150px;
            font-size: 45px;
        }
    }

    .item{
        position: relative;
    }

    .item:first-child{
        margin-top: 25px;
    }

    .bg{
        width: 100%;
        height: 100%;
        object-fit: cover;
        -webkit-filter: blur(1.5px);
        filter: blur(1.5px);
    }
    .title{
        position: absolute;
        width: 100%;
        height: 100%;
        text-align: center;
        color: white;
        font-weight: bold;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        left: 0;
        text-indent: 12px;
    }
    .title:hover{
        text-decoration: underline;
    }
    .date{
        position: absolute;
        bottom: 5px;
        right: 5px;
        color: white;
        font-size: 12px;
    }
</style>


