<template>
    <div class="content">
        <span class="title" v-on:click="show">{{ current.tag }}</span>
        <nav class="type">
            <ul  ref="list" :style="{ height:countHeight + 'px' }">
                <li :style="{ height:liHeight + 'px' }" v-for="item in data" :key="item.id" @click="change(item.id)">{{ item.tag }}</li>
            </ul>
        </nav>
        <Show ref="show"></Show>
    </div>
</template>

<script>
import Show from '@/components/Show'
export default {
    components:{
        Show
    },
    data(){
        return{
            liHeight:35,
            countHeight:0
        }
    },
    computed:{
        data(){
            return this.$store.state.question.data
        },
        current(){
            return this.$store.state.question.current
        },
        md(){
            return this.$store.state.question.md
        }
    },
    methods:{
        show(){
            this.countHeight = this.countHeight == 0 ? this.liHeight * this.$refs.list.children.length : 0
        },
        change(id){
            this.$store.commit("setCurrentQuestion",this.data[id-1])
            this.loadMd(id)
            this.show()
        },
        initJsonData(){
            this.$axios.get("/static/data/question.json?s="+new Date().getTime())
            .then(res => {
                this.$store.commit("setQuestionData",res.data)
                this.loadMd(this.current.id)
            })
            .catch(err => {
                console.log(err)
            })
        },
        loadMd(id){
            // md.jqwong.cn/question/{id}
            let file = id + ".md"
        
            this.$axios.get("https://md.jqwong.cn/question/"+file)
            .then(res => {
                this.$store.commit("setQuestionMD",res.data)
                this.$refs.show.$emit('getted')
                //console.log(res.data)
            })
            .catch(err => {
                console.log(err)
            })
        }
    },
    created(){
        if(this.data.length == 0){
            this.initJsonData()
        }
    }
}
</script>

<style scoped>
    
    .title{
        width: 60%;
        margin: 0 auto;
        font-size: 1.5rem;
        display: block;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 10px;
        background: #d1ecf1;
        color: #0c5460;
        border: 1px solid #bee5eb;
        cursor: pointer;
    }

    .type{
        width: 60%;
        margin: 0 auto;
        text-align: center;
        cursor: pointer;
    }
    .type ul{
        padding: 0;
        margin: 0;
        overflow: hidden;
        transition: height .2s ease-in-out;
    }
    .type li{
        line-height: 35px;
        list-style: none;
        background: #e2e3e5;
        color: #383d41;
        border:1px solid #d6d8db;
    }
    .type li:active{
        color: #004085;
        background: #cce5ff;
    }
</style>


