<template>
    <div class="header">
        <span class="header-tag">Jqwong's Blog</span>
        <nav class="header-nav">
            <ul>
                <li><router-link to="/">主页</router-link></li>
                <li><router-link to="/article">文章</router-link></li>
                <li><router-link to="/note">日志</router-link></li>
                <li><router-link to="/question">问题</router-link></li>
            </ul>
        </nav>
        <div class="box-search" v-if="this.$route.name == 'article' || this.$route.name == 'search'">
            <input type="text" class="input-search" ref="inputSearch"/>
            <img src="/static/img/search.svg" class="ico-search" v-on:click="search()"/>
        </div>
    </div>
</template>

<script>
export default {
    name:'Header',
    methods:{
        search(){
            let v = this.$refs.inputSearch.value
            if(v == "")
                return
            this.$router.push({ name:"search",query:{ key:v,type:this.$route.name } })
        }
    }
}
</script>

<style scoped>
    @media screen and (max-width:1024px) {
        .header-tag{
            font-size: 2.3rem;
        }
        .header-nav{
            width: 70%;
        }
        .header-nav li{
            padding: 0;
        }
    }
    @media screen and (max-width:1440px) and (min-width:1024px) {
        .header-tag{
            font-size: 3rem;
        }
        .header-nav{
            width: 40%;
        }
        .header-nav li{
            padding: 0 5px 0 5px;
        }
    }
    @media screen and (max-width:1920px) and (min-width: 1440px) {
        .header-tag{
            font-size: 3.5rem;
        }
        .header-nav{
            width: 30%;
            padding: 15px 0 15px 0;
        }
        .header-nav li{
            padding: 0 5px 0 5px;
        }
    }

    .header{
        padding-top: 30px;
        padding-bottom: 15px;
        text-align: center;
    }

    .header-tag{
        display: block;
        width: 100%;
        font-family: Cambria,Cochin,Georgia,Times,Times New Roman,serif;
        font-weight: bolder
    }

    .header-nav{
        height: 30px;
        line-height: 30px;
        margin: 0 auto;
        margin-top: 15px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .header-nav ul{
        padding: 0;
        margin: 0 auto;
    }

    .header-nav li{
        list-style: none;
        float: left;
        margin: 0 5px 0 5px;
    }

    .header-nav a{
        font-size: 1.2rem;
        text-decoration: none;
        color: black;
        transition: color .3s ease-in-out;
        position: relative;
        padding-bottom: 10px;
    }

    .header-nav a::after{
        content: '';
        display: block;
        width: 100%;
        height: 3px;
        position: absolute;
        left: 0;
        bottom: 0;
        background: salmon;
        transform-origin: center center;
        transform: scaleX(0);
    }

    .header-nav a.router-link-exact-active{
        color: salmon;
    }
    .header-nav a.router-link-exact-active::after{
        transform: scaleX(1);
    }

    .header-nav li:hover a{
        color: salmon;
    }
    
    .header-nav li:hover a:after{
        color: salmon;
        animation: show .2s ease-in-out forwards;
    }

    .box-search{
        width: 200px;
        margin: 0 auto;
        position: relative;
    }

    .input-search{
        width: 150px;
        outline: none;
        border-radius: 3px;
        border: 2px solid #dadada;
        padding: 10px 40px 10px 10px;
    }

    .ico-search{
        position: absolute;
        right: 5px;
        top: 8px;
        cursor: pointer;
    }

    .input-search:focus{
        border: 2px solid salmon;
    }

    @keyframes show{
        from{
            transform: scaleX(0);
        }
        to{
            transform: scaleX(1);
        }
    }
</style>


