<template>
    <div class="state">
        <span class="loading" v-if="loading">
            <img src="/static/img/loading.gif"/>
            数据加载中...
        </span>
        <span class="loaded" v-if="loaded"> 
            <img src="/static/img/success.svg"/>
            已全部加载完成~
        </span>
        <span class="empty" v-if="empty"> 
            <img src="/static/img/null.svg"/>
            没有找到数据~
        </span>
        <span class="error" v-if="error">  
            <img src="/static/img/error.svg"/>
            发生未知错误...
        </span>
        
    </div>
</template>


<script>
export default {
    props:{
        loading:Boolean,
        loaded:Boolean,
        error:Boolean,
        empty:Boolean
    }
}
</script>


<style scoped>
    .state{
        margin: 25px 0 25px 0;
    }
    .state span{
        width: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px 0 5px 0;
        margin: 0 auto;
    }

    .state img{
        width: 25px;
        margin-right: 10px;
    }

    .loaded{
        color: #fff;
        background-color: #007bff;
        border-color: #007bff;
    }

    .empty{
        flex-direction: column;
        align-items: center;
        color: #212529;
        background-color: #ffc107;
        border-color: #ffc107;
    }

    .empty img{
        width: 70px;
        margin-bottom: 10px;
    }

    .error{
        color: #fff;
        background-color: #dc3545;
        border-color: #dc3545;
    }
</style>


