<template>
    <div>
        <div class="info">
            <div class="item" v-for="item in data" :key="item.fileId">
                <!-- Article link -->
                <span class="title"><router-link :to="{ name:'article-detail',params:{ id:item.fileId } }">{{ item.title }}</router-link></span>
                <span class="sub-title">{{ item.subTitle }}</span>
                <img class="bg" v-lazy="item.imgUrl"/>
                <div class="desc">
                    <div class="tag">
                        <span v-for="tag in item.tag" :key="tag">{{tag}}</span>
                    </div>
                    <span class="date">{{ item.date }}</span>
                </div>
                <span class="hr"></span>
            </div>
        </div>
        <StateComponent :loading="loading" :loaded="loaded"/>
    </div>
</template>

<script>
import StateComponent from '../components/State.vue'
export default {
    name:'Article',
    components:{
        StateComponent
    },
    computed:{
        data:{
            get(){
             return this.$store.state.article.data   
            }
        },
        length(){
            return this.$store.state.article.length
        },
        loading(){
            return this.$store.state.article.loading
        },
        loaded(){
            return this.$store.state.article.loaded
        }
    },
    methods:{
        initJsonData:function(){
            let timestamp = Date.parse(new Date())
            this.$axios.get('/static/data/article.json?s='+timestamp)
            .then(res => {
                this.$store.commit('setArticleData',res.data)
                this.loadData()
            })
            .catch(err => {
                console.log(err)
            })
        },
        addData:function(data){
            this.$store.commit('addArticleData',data)
        },
        loadData:function(){
            this.$store.dispatch('loadArticleData').then(res => {
                this.addData(res)
            })
        }
    },
    created(){
        if(this.length == 0){
            this.initJsonData()
        }
    },
    mounted(){
        this.$on('inBottom',()=>{
            this.loadData()
        })
        this.$Lazyload.$on('loaded', function (listener) {
            listener.el.style.opacity = 1
        })
    }
}
</script>


<style scoped>
    @media screen and (max-width:1024px) {
        .item{
            width: 70%;
        }
        .bg{
            height: 200px;
        }

        .desc{
            display: flex !important;
            flex-direction: column;
            align-items: center;

        }

        .tag{
            margin-left: 0 !important;
            margin-top: 5px;
        }

        .date{
            position: relative !important;
            right: 0 !important;
            margin-top: 10px;
        }
        .hr{
            margin-top: 10px;
        }
        .desc{
            padding-top: 10px;
        }
    }
    @media screen and (max-width:1440px) and (min-width:1024px) {
        .item{
            width: 60%;
        }
        .bg{
            height: 300px;
        }
        .hr{
            margin-top: 15px;
        }
        .desc{
            padding-top: 10px;
        }
    }
    @media screen and (max-width:1920px) and (min-width: 1440px) {
        .item{
            width: 45%;
        }
        .bg{
            height: 400px;
        }
        .hr{
            margin-top: 15px;
        }
        .desc{
            padding-top: 15px;
        }
    }

    .info{
        margin-bottom: 25px;
    }

    .item{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        margin-top: 50px;
        text-align: center;
    }

    .item:first-child{
        margin-top: 25px;
    }

    .title{
        width: 80%;
        font-size: 2rem;
        font-family: Lucida Sans,Lucida Sans Regular,Lucida Grande,Lucida Sans Unicode,Geneva,Verdana,sans-serif;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 5px;
    }

    .title>a{
        color: black;
        text-decoration: none;
    }
    .title>a:hover{
        text-decoration: underline;
    }

    .sub-title{
        width: 80%;
        font-size: .7rem;
        padding-bottom: 3px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
         margin-bottom: 3px;
    }

    .bg{
        width: 100%;
        border-radius: 15px;
        box-shadow: 1px 3px 15px #dadada;
        object-fit: cover;
        opacity: .5;
        transition: opacity .2s ease-in-out;
    }

    .desc{
        width: 100%;
        position: relative;
        font-size: .8rem;
    }

    .tag{
        float: left;
        margin-left: 45px;
    }

    .tag span{
        padding: 2px 5px;
        background: #dadada;
        margin-right: 3px;
    }

    .date{
        position: absolute;
        right: 45px;
    }

    .hr{
        display: block;
        width: 50%;
        height: 1px;
        background: #dadada;
        opacity: .5;
    }
</style>


