// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import VueLazyload from 'vue-lazyload'
Vue.use(Vuex)
Vue.use(VueLazyload)
Vue.prototype.$axios = axios
Vue.config.productionTip = false

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: '/static/default.png',
  loading: '/static/default.png',
  attempt: 1
})

const mIndex = {
  state:{
    height:0
  },
  mutations:{
    setHeight(state,v){
      state.height = v
    }
  }
}

const mArticle = {
  state:{
    data:[],
    length:0,
    allData:[],
    maxSize:0,
    itemSize:20,
    page:0,
    loading:false,
    loaded:false
  },
  mutations:{
    setArticleData(state,v){
      state.allData = v.data
      state.maxSize = state.allData.length
    },
    addArticleData(state,v){
      state.data.push(...v)
      state.length = state.data.length
    },
    
  },
  actions:{
    loadArticleData(payload){
      if(payload.state.loaded)
        return
      payload.state.loading = true
      let start = payload.state.page * payload.state.itemSize
      let end   = start + payload.state.itemSize
      end = end > payload.state.maxSize ? payload.state.maxSize : end
      let arr   = payload.state.allData.slice(start,end)
      payload.state.loading = false
      if(end == payload.state.maxSize)
        payload.state.loaded = true
      payload.state.page++
      return arr
    },
    loadArticleInfo(payload,id){
      var arr =  payload.state.allData.filter(item => parseInt(item.fileId) == id)
      return arr
    }
  }
}

const mQuestion = {
  state:{
    data:[],
    current:{},
    md:""
  },
  mutations:{
    setQuestionData(state,v){
      state.data    = v.data
      state.current = v.data[0]
    },
    setCurrentQuestion(state,v){
      state.current = v
    },
    setQuestionMD(state,v){
      state.md = v;
    }
  }
}

const store = new Vuex.Store({
  modules:{
    index:mIndex,
    article:mArticle,
    question:mQuestion
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



